import React from 'react'
import s from './Message.module.css'
import message_ava from '../../../../assets/images/post_ava.jpg'

const Message = (props) => {
    return (
      <div className={s.message}>
        <img src={message_ava} alt=""/>
        <div className={s.text}>
          {props.message}
        </div>
      </div>
    ) 
}

export default Message;