import React from 'react';
import s from './NewMessage.module.css';
import { reduxForm } from 'redux-form';
import { Textarea, createField } from '../../../common/FormControls/FormsControls';
import { required, maxLengthCreator } from '../../../../utls/validators/validators';

const maxLength100 = maxLengthCreator(100);

const NewMessage = (props) => {
  const onSubmitNewMessage = (value) => {
      props.addMessage(value.newMessage);
    }
    return (
      <div className={s.new_message}>
        <NewMessageReduxForm  onSubmit={onSubmitNewMessage}/>
      </div>
    ) 
}

const NewMessageForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      {createField("Новое сообщение", "newMessage", [required, maxLength100], Textarea)}
      <button>Отправить</button>
    </form>
  ) 
}

const NewMessageReduxForm = reduxForm ({
  form: 'newMessage' 
})(NewMessageForm)


export default NewMessage;